<template>
  <container :title="title">
    <list :isPager="false">
      <thead slot="thead">
      <tr>
        <th>{{lang.id}}</th>
        <th>{{lang.backupname}}</th>
        <th>{{lang.create_time}}</th>
        <th>{{lang.fileSize}}</th>
        <th width="175">{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="(vol, index) in list">
        <td>{{index}}</td>
        <td><span class="label label-danger">{{vol.filename}}</span></td>
        <td>{{vol['filectime']}}</td>
        <td><span class="label label-success">{{vol['filesize']}}</span></td>
        <v-td type="opt" :value="vol['uuid']"
              :show-download="true"
              :show-recovery="true"/>

      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd} from "@/components"

  export default {

    components: {VTd,Container, List},
    data(){
      return{
        title:this.$t('backup.index'),
        lang:this.$t('backup'),
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      }
    },
    mounted(){
      const url = '/api/database/backup'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



